
.tab {
  height: 40px;
  line-height: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  border: none;
}
.tab:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  background: rgba(7,17,27,0.1);
  width: 100%;
  height: 1px;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 14px;
  color: #4d555d;
  text-align: center;
}
.tab .tab-item .active {
  color: #f00;
}


